import { getType } from 'typesafe-actions';
import { loginFailure, loginRequest, loginSuccess, logout, } from './sessionActions';
export const defaultState = {
    token: null,
    loginFetchStatus: 0 /* none */,
    loginError: null,
};
export const stateKey = 'session';
export function sessionReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(loginRequest):
            return Object.assign({}, state, { loginFetchStatus: 1 /* inProgress */ });
        case getType(loginSuccess):
            return Object.assign({}, state, { token: action.payload, loginError: null, loginFetchStatus: 2 /* complete */ });
        case getType(loginFailure):
            return Object.assign({}, state, { token: null, loginFetchStatus: 2 /* complete */, loginError: action.payload });
        case getType(logout):
            return Object.assign({}, defaultState);
        default:
            return state;
    }
}
