import { runExport } from 'api/ocpOnAwsExport';
import { createAsyncAction } from 'typesafe-actions';
export const { request: fetchOcpOnAwsExportRequest, success: fetchOcpOnAwsExportSuccess, failure: fetchOcpOnAwsExportFailure, } = createAsyncAction('ocpOnAwsExport/request', 'ocpOnAwsExport/success', 'ocpOnAwsExport/failure')();
export function exportReport(reportType, query) {
    return (dispatch, getState) => {
        dispatch(fetchOcpOnAwsExportRequest());
        runExport(reportType, query)
            .then(res => {
            dispatch(fetchOcpOnAwsExportSuccess(res.data));
        })
            .catch(err => {
            dispatch(fetchOcpOnAwsExportFailure(err));
        });
    };
}
