import axios from 'axios';
import { ocpOnAwsReportTypePaths } from './ocpOnAwsReports';
export function runExport(reportType, query) {
    const path = ocpOnAwsReportTypePaths[reportType];
    return axios.get(`${path}?${query}`, {
        headers: {
            Accept: 'text/csv',
        },
    });
}
