import { getType } from 'typesafe-actions';
import { setWidgetTab } from './ocpOnAwsDashboardActions';
import { computeWidget, costSummaryWidget, cpuWidget, databaseWidget, memoryWidget, networkWidget, storageWidget, volumeWidget, } from './ocpOnAwsDashboardWidgets';
export const defaultState = {
    currentWidgets: [
        costSummaryWidget.id,
        computeWidget.id,
        storageWidget.id,
        networkWidget.id,
        databaseWidget.id,
        cpuWidget.id,
        memoryWidget.id,
        volumeWidget.id,
    ],
    widgets: {
        [costSummaryWidget.id]: costSummaryWidget,
        [computeWidget.id]: computeWidget,
        [databaseWidget.id]: databaseWidget,
        [networkWidget.id]: networkWidget,
        [storageWidget.id]: storageWidget,
        [cpuWidget.id]: cpuWidget,
        [memoryWidget.id]: memoryWidget,
        [volumeWidget.id]: volumeWidget,
    },
};
export function ocpOnAwsDashboardReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(setWidgetTab):
            return Object.assign({}, state, { widgets: Object.assign({}, state.widgets, { [action.payload.id]: Object.assign({}, state.widgets[action.payload.id], { currentTab: action.payload.tab }) }) });
        default:
            return state;
    }
}
