import axios from 'axios';
export const awsReportTypePaths = {
    ["cost" /* cost */]: 'reports/aws/costs/',
    ["database" /* database */]: 'reports/aws/costs/',
    ["network" /* network */]: 'reports/aws/costs/',
    ["storage" /* storage */]: 'reports/aws/storage/',
    ["instance_type" /* instanceType */]: 'reports/aws/instance-types/',
    ["tag" /* tag */]: 'tags/aws/',
};
export function runReport(reportType, query) {
    const path = awsReportTypePaths[reportType];
    const insights = window.insights;
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.get(`${path}?${query}`);
        });
    }
    else {
        return axios.get(`${path}?${query}`);
    }
}
