import axios from 'axios';
export const ocpOnAwsReportTypePaths = {
    ["cost" /* cost */]: 'reports/openshift/infrastructures/aws/costs/',
    ["cpu" /* cpu */]: 'reports/openshift/compute/',
    ["database" /* database */]: 'reports/openshift/infrastructures/aws/costs/',
    ["instance_type" /* instanceType */]: 'reports/openshift/infrastructures/aws/instance-types/',
    ["memory" /* memory */]: 'reports/openshift/memory/',
    ["network" /* network */]: 'reports/openshift/infrastructures/aws/costs/',
    ["storage" /* storage */]: 'reports/openshift/infrastructures/aws/storage/',
    ["tag" /* tag */]: 'tags/openshift/infrastructures/aws/',
    ["volume" /* volume */]: 'reports/openshift/volumes/',
};
export function runReport(reportType, query) {
    const path = ocpOnAwsReportTypePaths[reportType];
    const insights = window.insights;
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.get(`${path}?${query}`);
        });
    }
    else {
        return axios.get(`${path}?${query}`);
    }
}
