import { getQueryForWidget, getQueryForWidgetTabs, ocpOnAwsDashboardDefaultFilters, ocpOnAwsDashboardStateKey, ocpOnAwsDashboardTabFilters, } from './ocpOnAwsDashboardCommon';
export const selectOcpOnAwsDashboardState = (state) => state[ocpOnAwsDashboardStateKey];
export const selectWidgets = (state) => selectOcpOnAwsDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpOnAwsDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const defaultFilter = Object.assign({}, ocpOnAwsDashboardDefaultFilters, (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign({}, ocpOnAwsDashboardTabFilters, (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign({}, defaultFilter, { time_scope_value: -2 })),
        current: getQueryForWidget(defaultFilter),
        tabs: getQueryForWidgetTabs(widget, Object.assign({}, tabsFilter, { resolution: 'monthly' })),
    };
};
