import { getType } from 'typesafe-actions';
import { fetchOcpReportFailure, fetchOcpReportRequest, fetchOcpReportSuccess, } from './ocpReportsActions';
const defaultState = {
    byId: new Map(),
    fetchStatus: new Map(),
    errors: new Map(),
};
export function ocpReportsReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(fetchOcpReportRequest):
            return Object.assign({}, state, { fetchStatus: new Map(state.fetchStatus).set(action.payload.reportId, 1 /* inProgress */) });
        case getType(fetchOcpReportSuccess):
            return Object.assign({}, state, { fetchStatus: new Map(state.fetchStatus).set(action.meta.reportId, 2 /* complete */), byId: new Map(state.byId).set(action.meta.reportId, Object.assign({}, action.payload, { timeRequested: Date.now() })), errors: new Map(state.errors).set(action.meta.reportId, null) });
        case getType(fetchOcpReportFailure):
            return Object.assign({}, state, { fetchStatus: new Map(state.fetchStatus).set(action.meta.reportId, 2 /* complete */), errors: new Map(state.errors).set(action.meta.reportId, action.payload) });
        default:
            return state;
    }
}
