import { runExport } from 'api/awsExport';
import { createAsyncAction } from 'typesafe-actions';
export const { request: fetchAwsExportRequest, success: fetchAwsExportSuccess, failure: fetchAwsExportFailure, } = createAsyncAction('awsExport/request', 'awsExport/success', 'awsExport/failure')();
export function exportReport(reportType, query) {
    return (dispatch, getState) => {
        dispatch(fetchAwsExportRequest());
        runExport(reportType, query)
            .then(res => {
            dispatch(fetchAwsExportSuccess(res.data));
        })
            .catch(err => {
            dispatch(fetchAwsExportFailure(err));
        });
    };
}
