import { stateKey } from './awsExportReducer';
export const selectExportState = (state) => state[stateKey];
export const selectExport = (state) => selectExportState(state).export;
export const selectExportFetchStatus = (state) => selectExportState(state).exportFetchStatus;
export const selectExportError = (state) => selectExportState(state).exportError;
