import { runExport } from 'api/azureExport';
import { createAsyncAction } from 'typesafe-actions';
export const { request: fetchAzureExportRequest, success: fetchAzureExportSuccess, failure: fetchAzureExportFailure, } = createAsyncAction('azureExport/request', 'azureExport/success', 'azureExport/failure')();
export function exportReport(reportType, query) {
    return (dispatch, getState) => {
        dispatch(fetchAzureExportRequest());
        runExport(reportType, query)
            .then(res => {
            dispatch(fetchAzureExportSuccess(res.data));
        })
            .catch(err => {
            dispatch(fetchAzureExportFailure(err));
        });
    };
}
