import { runExport } from 'api/ocpExport';
import { createAsyncAction } from 'typesafe-actions';
export const { request: fetchOcpExportRequest, success: fetchOcpExportSuccess, failure: fetchOcpExportFailure, } = createAsyncAction('ocpExport/request', 'ocpExport/success', 'ocpExport/failure')();
export function exportReport(reportType, query) {
    return (dispatch, getState) => {
        dispatch(fetchOcpExportRequest());
        runExport(reportType, query)
            .then(res => {
            dispatch(fetchOcpExportSuccess(res.data));
        })
            .catch(err => {
            dispatch(fetchOcpExportFailure(err));
        });
    };
}
