import { deleteProvider as apiRemoveSource, fetchProviders as apiGetSources, } from 'api/providers';
import { deleteDialogActions } from 'store/sourceDeleteDialog';
import { createAsyncAction, createStandardAction } from 'typesafe-actions';
export const updateFilterToolbar = createStandardAction('fetch/source/filter')();
export const { request: fetchSourcesRequest, success: fetchSourcesSuccess, failure: fetchSourcesFailure, } = createAsyncAction('fetch/source/request', 'fetch/source/success', 'fetch/source/failure')();
export const fetchSources = (query = '') => {
    return (dispatch) => {
        dispatch(fetchSourcesRequest());
        return apiGetSources(query)
            .then(res => {
            dispatch(fetchSourcesSuccess(res));
        })
            .catch(err => {
            dispatch(fetchSourcesFailure(err));
        });
    };
};
export const { request: removeSourceRequest, success: removeSourceSuccess, failure: removeSourceFailure, } = createAsyncAction('remove/source/request', 'remove/source/success', 'remove/source/failure')();
export const removeSource = uuid => {
    return (dispatch) => {
        dispatch(removeSourceRequest());
        dispatch(deleteDialogActions.processing());
        return apiRemoveSource(uuid)
            .then(res => {
            fetchSources()(dispatch);
            dispatch(deleteDialogActions.closeModal());
        })
            .catch(err => {
            dispatch(removeSourceFailure(err));
            dispatch(deleteDialogActions.error(err));
        });
    };
};
