import axios from 'axios';
export function fetchCostModels(query = '') {
    const insights = window.insights;
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.get(`costmodels/${query && '?'}${query}`);
        });
    }
    return axios.get(`costmodels/${query && '?'}${query}`);
}
export function addCostModel(request) {
    const insights = window.insights;
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.post('costmodels/', request);
        });
    }
    else {
        return axios.post('costmodels/', request);
    }
}
export function updateCostModel(uuid, request) {
    const insights = window.insights;
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.put(`costmodels/${uuid}/`, request);
        });
    }
    return axios.put(`costmodels/${uuid}/`, request);
}
export function deleteCostModel(uuid) {
    const insights = window.insights;
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.delete(`costmodels/${uuid}/`);
        });
    }
    return axios.delete(`costmodels/${uuid}/`);
}
