import { getQuery } from 'api/ocpOnAwsQuery';
export const ocpOnAwsDashboardStateKey = 'ocpOnAwsDashboard';
export const ocpOnAwsDashboardDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
};
export const ocpOnAwsDashboardTabFilters = Object.assign({}, ocpOnAwsDashboardDefaultFilters, { limit: 3 });
// Todo: cluster, project, node
export function getGroupByForTab(tab) {
    switch (tab) {
        case "projects" /* projects */:
            return { project: '*' };
        case "clusters" /* clusters */:
            return { cluster: '*' };
        case "nodes" /* nodes */:
            return { node: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(filter = ocpOnAwsDashboardDefaultFilters) {
    const query = {
        filter,
    };
    return getQuery(query);
}
export function getQueryForWidgetTabs(widget, filter = ocpOnAwsDashboardDefaultFilters) {
    const query = {
        filter,
        group_by: getGroupByForTab(widget.currentTab),
    };
    return getQuery(query);
}
