import { login as apiLogin, logout as apiLogout, } from 'api/session';
import { createAction, createAsyncAction } from 'typesafe-actions';
export const { request: loginRequest, success: loginSuccess, failure: loginFailure, } = createAsyncAction('session/login/request', 'session/login/success', 'session/login/failure')();
export function login(request) {
    return (dispatch) => {
        dispatch(loginRequest());
        return apiLogin(request)
            .then(response => {
            dispatch(loginSuccess('token'));
        })
            .catch(err => {
            dispatch(loginFailure(err));
        });
    };
}
export const logout = createAction('session/logout', resolve => {
    return () => {
        apiLogout();
        return resolve();
    };
});
