import { getType } from 'typesafe-actions';
import { closeModal, error, openModal, processing } from './actions';
export const stateKey = 'deleteDialog';
export const defaultState = {
    isOpen: false,
    isProcessing: false,
    isError: false,
    name: '',
    type: '',
    onDelete: null,
};
export const reducer = (state = defaultState, action) => {
    switch (action.type) {
        case getType(openModal):
            return Object.assign({}, state, action.payload, { isOpen: true });
        case getType(processing):
            return Object.assign({}, state, { isProcessing: true });
        case getType(closeModal):
            return defaultState;
        case getType(error):
            return Object.assign({}, state, { isError: true });
        default:
            return state;
    }
};
