import { getType } from 'typesafe-actions';
import { addSourceFailure, addSourceRequest, addSourceSuccess, cancelOnboarding, checkSourceKindCheckList, closeModal, displayConfirm, hideConfirm, openModal, updateArn, updateClusterID, updateCreds, updateDataSource, updateName, updateS3BucketName, updateSourceKindCheckList, updateType, } from './actions';
export const defaultState = {
    type: '',
    typeValid: true,
    typeDirty: false,
    name: '',
    nameValid: true,
    nameDirty: false,
    clusterId: '',
    clusterIdValid: true,
    clusterIdDirty: false,
    s3BucketName: '',
    s3BucketNameValid: true,
    s3BucketNameDirty: false,
    arn: '',
    arnValid: true,
    arnDirty: false,
    sourceKindChecks: {
        install_openshift: false,
        install_others: false,
    },
    azure: {
        credentials: {},
        dataSource: {},
    },
    isOpen: false,
    isConfirmShown: false,
    apiStatus: 0 /* none */,
    apiErrors: null,
};
const areAllChecked = state => Object.keys(state).every(k => state[k]);
const setAll = (state, value) => {
    return Object.keys(state)
        .map(k => ({ [k]: value }))
        .reduce((acc, curr) => (Object.assign({}, acc, curr)));
};
export const stateKey = 'onboarding';
export function reducer(state = defaultState, action) {
    switch (action.type) {
        case getType(updateType):
            return Object.assign({}, state, { type: action.payload, typeValid: action.meta(action.payload), typeDirty: true });
        case getType(updateName):
            return Object.assign({}, state, { name: action.payload, nameValid: action.meta(action.payload), nameDirty: true });
        case getType(updateClusterID):
            return Object.assign({}, state, { clusterId: action.payload, clusterIdValid: action.meta(action.payload), clusterIdDirty: true });
        case getType(updateS3BucketName):
            return Object.assign({}, state, { s3BucketName: action.payload, s3BucketNameValid: action.meta(action.payload), s3BucketNameDirty: true });
        case getType(updateArn):
            return Object.assign({}, state, { arn: action.payload, arnValid: action.meta(action.payload), arnDirty: true });
        case getType(updateSourceKindCheckList):
            return Object.assign({}, state, { sourceKindChecks: Object.assign({}, state.sourceKindChecks, { [action.payload.item]: action.payload.value }) });
        case getType(checkSourceKindCheckList):
            const skVal = areAllChecked(state.sourceKindChecks) ? false : true;
            return Object.assign({}, state, { sourceKindChecks: setAll(state.sourceKindChecks, skVal) });
        case getType(updateCreds):
            return Object.assign({}, state, { azure: Object.assign({}, state.azure, { credentials: Object.assign({}, state.azure.credentials, { [action.payload.name]: {
                            value: action.payload.value,
                            valid: action.meta(action.payload.value),
                            dirty: true,
                        } }) }) });
        case getType(updateDataSource):
            return Object.assign({}, state, { azure: Object.assign({}, state.azure, { dataSource: Object.assign({}, state.azure.dataSource, { [action.payload.name]: {
                            value: action.payload.value,
                            valid: action.meta(action.payload.value),
                            dirty: true,
                        } }) }) });
        case getType(cancelOnboarding):
            return defaultState;
        case getType(openModal):
            return Object.assign({}, state, { isOpen: true });
        case getType(closeModal):
            return Object.assign({}, state, { isOpen: false });
        case getType(displayConfirm):
            return Object.assign({}, state, { isOpen: false, isConfirmShown: true });
        case getType(hideConfirm):
            return Object.assign({}, state, { isOpen: true, isConfirmShown: false });
        case getType(addSourceRequest):
            return Object.assign({}, state, { apiStatus: 1 /* inProgress */, apiErrors: null });
        case getType(addSourceFailure):
            return Object.assign({}, state, { apiStatus: 2 /* complete */, apiErrors: action.payload });
        case getType(addSourceSuccess):
            return Object.assign({}, defaultState, { apiStatus: 2 /* complete */ });
        default:
            return state;
    }
}
