import axios from 'axios';
// See: http://koku-koku-dev.1b13.insights.openshiftapps.com/apidoc/index.html#api-Provider-createProvider
export function addProvider(request) {
    const insights = window.insights;
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.post('providers/', request);
        });
    }
    else {
        return axios.post('providers/', request);
    }
}
// See: http://koku-koku-dev.1b13.insights.openshiftapps.com/apidoc/index.html#api-Provider-GetProvider
export function fetchProviders(query) {
    const insights = window.insights;
    const queryString = query ? `?${query}` : '';
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.get(`providers/${queryString}`);
        });
    }
    else {
        return axios.get(`providers/${queryString}`);
    }
}
export function deleteProvider(uuid) {
    const insights = window.insights;
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.delete(`providers/${uuid}/`);
        });
    }
    else {
        return axios.delete(`providers/${uuid}/`);
    }
}
