import { runReport, } from 'api/ocpOnAwsReports';
import { createStandardAction } from 'typesafe-actions';
import { dropCurrentMonthData } from 'utils/dropCurrentMonthData';
import { getReportId } from './ocpOnAwsReportsCommon';
import { selectReport, selectReportFetchStatus, } from './ocpOnAwsReportsSelectors';
const expirationMS = 30 * 60 * 1000; // 30 minutes
export const fetchOcpOnAwsReportRequest = createStandardAction('ocpOnAwsReports/request')();
export const fetchOcpOnAwsReportSuccess = createStandardAction('ocpOnAwsReports/success')();
export const fetchOcpOnAwsReportFailure = createStandardAction('ocpOnAwsReports/failure')();
export function fetchReport(reportType, query) {
    return (dispatch, getState) => {
        if (!isReportExpired(getState(), reportType, query)) {
            return;
        }
        const meta = {
            reportId: getReportId(reportType, query),
        };
        dispatch(fetchOcpOnAwsReportRequest(meta));
        runReport(reportType, query)
            .then(res => {
            const repsonseData = dropCurrentMonthData(res, query);
            dispatch(fetchOcpOnAwsReportSuccess(repsonseData, meta));
        })
            .catch(err => {
            dispatch(fetchOcpOnAwsReportFailure(err, meta));
        });
    };
}
function isReportExpired(state, reportType, query) {
    const report = selectReport(state, reportType, query);
    const fetchStatus = selectReportFetchStatus(state, reportType, query);
    if (fetchStatus === 1 /* inProgress */) {
        return false;
    }
    if (!report) {
        return true;
    }
    const now = Date.now();
    return now > report.timeRequested + expirationMS;
}
