import axios from 'axios';
export const azureReportTypePaths = {
    ["cost" /* cost */]: 'reports/azure/costs/',
    ["database" /* database */]: 'reports/azure/costs/',
    ["network" /* network */]: 'reports/azure/costs/',
    ["storage" /* storage */]: 'reports/azure/storage/',
    ["instance_type" /* instanceType */]: 'reports/azure/instance-types/',
    ["tag" /* tag */]: 'tags/azure/',
};
export function runReport(reportType, query) {
    const path = azureReportTypePaths[reportType];
    const insights = window.insights;
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.get(`${path}?${query}`);
        });
    }
    else {
        return axios.get(`${path}?${query}`);
    }
}
